"use client";

import CryptoHero from "./ui/crypto-hero";
import { CreditCard, DollarSign, Gem, Wallet } from "lucide-react";
import { useRouter } from "next/navigation";

export function Hero() {
    const router = useRouter();

    return (
        <CryptoHero
            className="pb-32" // Add padding for brands at bottom
            logo={<></>} // Empty logo to avoid duplication with global Navbar
            title="Complete Crypto Payment Gateway"
            // subtitle="for your business with Crypto"
            description="Our complete payment solution with simple integration helps you reach more customers, reduce fraudulent transactions, and reconcile with ease."
            emailPlaceholder="Enter your business email"
            ctaButton={{
                label: "Register Now",
                onClick: () => router.push('/signup'),
            }}
            cryptoCoins={[
                {
                    icon: <div className="text-white font-bold text-2xl">IDRX</div>,
                    size: 90,
                    position: { x: "10%", y: "10%" },
                    delay: 0,
                },
                {
                    icon: <DollarSign className="text-green-400 w-10 h-10" />,
                    size: 70,
                    position: { x: "15%", y: "60%" },
                    delay: 0.2,
                },
                {
                    icon: <div className="text-blue-400 font-bold text-xl">USDC</div>,
                    size: 80,
                    position: { x: "75%", y: "15%" },
                    delay: 0.4,
                },
                {
                    icon: <div className="text-teal-400 font-bold text-xl">USDT</div>,
                    size: 60,
                    position: { x: "85%", y: "70%" },
                    delay: 0.6,
                },
            ]}
            walletImage={
                <div className="relative w-80 h-[480px] bg-zinc-900 rounded-[3rem] border-8 border-zinc-800 shadow-2xl flex flex-col overflow-hidden">
                    {/* Phone Notion Mockup style */}
                    <div className="absolute top-0 left-1/2 -translate-x-1/2 w-32 h-6 bg-zinc-800 rounded-b-xl z-20"></div>

                    {/* Screen Content */}
                    <div className="flex-1 bg-black p-6 pt-12 relative overflow-hidden">
                        <div className="flex justify-between items-center mb-6">
                            <div>
                                <div className="text-xs text-zinc-500">Total Balance</div>
                                <div className="text-2xl font-bold text-white">$24,500.00</div>
                            </div>
                            <div className="w-8 h-8 rounded-full bg-green-500/20 flex items-center justify-center">
                                <Wallet size={16} className="text-green-500" />
                            </div>
                        </div>

                        <div className="grid grid-cols-2 gap-3 mb-6">
                            <div className="bg-zinc-900 p-3 rounded-xl">
                                <div className="bg-green-500/10 w-8 h-8 rounded-lg flex items-center justify-center mb-2">
                                    <CreditCard size={16} className="text-green-500" />
                                </div>
                                <div className="text-xs text-zinc-500">Revenue</div>
                                <div className="font-bold text-white">+$12k</div>
                            </div>
                            <div className="bg-zinc-900 p-3 rounded-xl">
                                <div className="bg-blue-500/10 w-8 h-8 rounded-lg flex items-center justify-center mb-2">
                                    <Gem size={16} className="text-blue-500" />
                                </div>
                                <div className="text-xs text-zinc-500">Active Subs</div>
                                <div className="font-bold text-white">854</div>
                            </div>
                        </div>

                        <div className="space-y-3">
                            <div className="text-xs text-zinc-500 mb-2">Recent Transactions</div>
                            {[1, 2, 3].map(i => (
                                <div key={i} className="flex items-center justify-between p-3 bg-zinc-900/50 rounded-xl border border-white/5">
                                    <div className="flex items-center gap-3">
                                        <div className="w-8 h-8 rounded-full bg-orange-500/10 flex items-center justify-center text-orange-500 font-bold text-xs">₿</div>
                                        <div>
                                            <div className="text-sm text-white font-medium">Subscription</div>
                                            <div className="text-[10px] text-zinc-500">2 mins ago</div>
                                        </div>
                                    </div>
                                    <div className="text-sm font-bold text-green-400">+$49.00</div>
                                </div>
                            ))}
                        </div>

                        {/* Bottom glow */}
                        <div className="absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t from-green-900/20 to-transparent pointer-events-none"></div>
                    </div>
                </div>
            }
            brands={[
                { name: "Netflix", logo: <span className="text-xl font-bold text-white">NETFLIX</span> },
                { name: "Spotify", logo: <span className="text-xl font-bold text-white">Spotify</span> },
                { name: "Gjek", logo: <span className="text-xl font-bold text-green-500">gojek</span> }, // Gojek style
                { name: "Tokped", logo: <span className="text-xl font-bold text-green-500">tokopedia</span> },
                { name: "Gooogle", logo: <span className="text-xl font-bold text-white">Google</span> },
            ]}
        />
    );
}
