"use client";

import { motion } from "framer-motion";
import Link from "next/link";

export function CTA() {
    return (
        <section className="py-24 relative overflow-hidden bg-gradient-to-b from-black to-zinc-900">
            <div className="container mx-auto px-4 md:px-6 relative z-10">
                <motion.div
                    initial={{ opacity: 0, scale: 0.95 }}
                    whileInView={{ opacity: 1, scale: 1 }}
                    viewport={{ once: true }}
                    className="bg-green-600 rounded-3xl p-12 md:p-20 text-center relative overflow-hidden"
                >
                    {/* Decorative Pattern */}
                    <div className="absolute inset-0 opacity-10 bg-[radial-gradient(circle_at_center,_var(--tw-gradient-stops))] from-white via-transparent to-transparent" />

                    <h2 className="text-3xl md:text-5xl font-bold text-white mb-6 relative z-10">
                        Ready to Accept Recurring Crypto Payments?
                    </h2>
                    <p className="text-green-100 text-xl mb-10 max-w-2xl mx-auto relative z-10">
                        Join hundreds of merchants already earning <span className="font-bold text-white">recurring</span> revenue on Base Stack.
                    </p>

                    <div className="flex flex-col sm:flex-row items-center justify-center gap-4 relative z-10">
                        <Link
                            href="/signup"
                            className="bg-white text-green-600 px-8 py-4 rounded-full text-lg font-bold transition-transform hover:scale-105 shadow-xl"
                        >
                            Get Started Now
                        </Link>
                    </div>
                </motion.div>
            </div>
        </section>
    );
}
