"use client";

import { motion } from "framer-motion";

export function HowItWorks() {
    const steps = [
        {
            step: 1,
            title: "Create Plan",
            description: "Define your subscription tiers, billing cycles, and pricing in stablecoins.",
        },
        {
            step: 2,
            title: "Share Link",
            description: "Send a unique checkout link to your customers or embed it on your site.",
        },
        {
            step: 3,
            title: "Get Paid",
            description: "Customers connect wallet & approve. Money flows automatically every month.",
        },
    ];

    return (
        <section id="how-it-works" className="py-24 relative overflow-hidden bg-black">
            {/* Background Glow */}
            <div className="absolute opacity-30 bottom-0 left-1/2 transform -translate-x-1/2 w-[800px] h-[400px] bg-green-500/20 blur-[120px] rounded-full pointer-events-none" />

            <div className="container mx-auto px-4 md:px-6 relative z-10">
                <div className="text-center mb-16">
                    <h2 className="text-3xl md:text-5xl font-bold text-white mb-4">
                        How It Works
                    </h2>
                    <p className="text-green-500 text-lg uppercase tracking-widest font-bold">
                        From setup to payment in minutes
                    </p>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-8 max-w-5xl mx-auto">
                    {steps.map((item, index) => (
                        <motion.div
                            key={index}
                            initial={{ opacity: 0, y: 20 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            viewport={{ once: true }}
                            transition={{ delay: index * 0.2 }}
                            className="relative group"
                        >
                            <div className="glass-panel p-8 rounded-2xl h-full border border-white/10 group-hover:border-green-500/50 transition-colors bg-gradient-to-br from-white/5 to-transparent">
                                <div className="absolute -top-4 -left-4 w-10 h-10 bg-green-500 text-black font-bold flex items-center justify-center rounded-lg shadow-lg shadow-green-500/20">
                                    {item.step}
                                </div>
                                <h3 className="text-2xl font-bold text-white mb-4 mt-2">{item.title}</h3>
                                <p className="text-zinc-400 leading-relaxed">
                                    {item.description}
                                </p>
                            </div>
                        </motion.div>
                    ))}
                </div>
            </div>
        </section>
    );
}
