"use client";

import { motion } from "framer-motion";
import { XCircle, Clock, FileWarning, StopCircle } from "lucide-react";

export function Problems() {
    const problems = [
        {
            icon: <XCircle className="w-8 h-8 text-red-400" />,
            title: "Manual Pain Process",
            description: "Tired of reminding clients to pay manually every month? It's a waste of time.",
        },
        {
            icon: <Clock className="w-8 h-8 text-orange-400" />,
            title: "Time Wasted",
            description: "Chasing payments takes away focus from building your actual product.",
        },
        {
            icon: <FileWarning className="w-8 h-8 text-yellow-400" />,
            title: "High Fees",
            description: "Traditional gateways charge 3-5% + fx fees. We charge <1%.",
        },
        {
            icon: <StopCircle className="w-8 h-8 text-zinc-400" />,
            title: "No Control",
            description: "risk of frozen funds or sudden account bans by centralized providers.",
        },
    ];

    return (
        <section id="problem" className="py-24 bg-zinc-900/50 relative">
            <div className="container mx-auto px-4 md:px-6">
                <div className="text-center mb-16">
                    <h2 className="text-3xl md:text-5xl font-bold text-white mb-4">
                        The Problems We Solve
                    </h2>
                    <p className="text-zinc-400 text-lg max-w-2xl mx-auto">
                        Traditional crypto payments are manual and frustrating. We make them automatic and effortless.
                    </p>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                    {problems.map((item, index) => (
                        <motion.div
                            key={index}
                            initial={{ opacity: 0, scale: 0.95 }}
                            whileInView={{ opacity: 1, scale: 1 }}
                            viewport={{ once: true }}
                            transition={{ delay: index * 0.1 }}
                            className="bg-black/40 border border-white/5 p-6 rounded-2xl hover:border-red-500/20 transition-colors"
                        >
                            <div className="mb-4 bg-white/5 w-14 h-14 rounded-full flex items-center justify-center">
                                {item.icon}
                            </div>
                            <h3 className="text-xl font-bold text-white mb-3">{item.title}</h3>
                            <p className="text-zinc-400 text-sm leading-relaxed">
                                {item.description}
                            </p>
                        </motion.div>
                    ))}
                </div>
            </div>
        </section>
    );
}
